import React, { useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import { useDispatch } from 'react-redux';
import { addToCart } from '../actions/cartActions';
import Button from 'react-bootstrap/Button';
import 'aos/dist/aos.css';
import AOS from 'aos';
const Pizza = (props) => {
  AOS.init({
    duration: 1000,
  });
  const [quantity, setQuantity] = useState(1);
  const [varient, setvarient] = useState('small');

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const changeVarientHandler = (e) => {
    setvarient(e.target.value);
  };
  const changeQuantityHandler = (e) => {
    setQuantity(e.target.value);
  };
  const dispatch = useDispatch();
  const handleToCart = () => {
    dispatch(addToCart(props.pizza, quantity, varient));
  };
  return (
    <div data-aos="fade-up" className="shadow p-3 mb-5 bg-white rounded">
      <div onClick={handleShow}>
        <h1>{props.pizza.name}</h1>
        <img
          src={props.pizza.image}
          className="img-fluid"
          style={{ height: '200px', width: '200px' }}
        ></img>
      </div>
      <div className="flex-container">
        <div className="w-100 m-1">
          <p>varient</p>
          <select
            className="form-control"
            value={varient}
            onChange={changeVarientHandler}
          >
            {props.pizza.varient.map((varient, index) => {
              return (
                <option key={index} value={varient}>
                  {varient}
                </option>
              );
            })}
          </select>
        </div>
        <div className="w-100 m-1">
          <p>Quantity</p>
          <select
            className="form-control"
            value={quantity}
            onChange={changeQuantityHandler}
          >
            {[...Array(10).keys()].map((x, i) => {
              return (
                <option key={x} value={i + 1}>
                  {i + 1}
                </option>
              );
            })}
          </select>
        </div>
      </div>
      <div className="flex-container">
        <div className="w-100">
          <h1 className="mt-1">
            Price : {props.pizza.prices[0][varient] * [quantity]} Rs/-
          </h1>
        </div>
        <div className="w-100">
          <button className="btn" onClick={handleToCart}>
            Add to cart
          </button>
        </div>
      </div>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>{props.pizza.name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <img
            src={props.pizza.image}
            className="img-fluid"
            style={{ height: '270px' }}
          />
          <p>{props.pizza.description}</p>
        </Modal.Body>
        <Modal.Footer>
          <Button className="btn" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};
export default Pizza;
