import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { addToCart, deleteFromCart } from '../actions/cartActions';
import Checkout from '../components/Checkout';
import 'aos/dist/aos.css';
import AOS from 'aos';

const CartScreen = () => {
  AOS.init({
    duration: 1000,
  });
  const dispatch = useDispatch();

  const cartStatus = useSelector((status) => status.cartReducer);
  const cartItems = cartStatus.cartItems;
  var subTotal = cartItems.reduce((x, item) => x + item.price, 0);
  var Total = cartItems.reduce(
    (x, item) => x + item.price + (18 / 100) * item.price,
    0
  );
  return (
    <div>
      <div data-aos="fade-down" className="row justify-content-center p-3">
        <div className="col-md-6">
          <h1 style={{ fontSize: '40px' }}>My Cart</h1>
          {cartItems.map((item, index) => {
            return (
              <div className="flex-container" key={`${item._id}-${index}`}>
                <div className="text-left m-1 w-100">
                  <h1>
                    {item.name} [{item.varient}]
                  </h1>
                  <h1>
                    price : {item.quantity} * {item.prices[0][item.varient]} ={' '}
                    {item.price}
                  </h1>
                  <h1 style={{ display: 'inline' }}>Quantity : </h1>
                  <i
                    className="fa fa-plus"
                    area-hidden="true"
                    style={{
                      color: 'red',
                      fontWeight: 'bold',
                      margin: '5px',
                      cursor: 'pointer',
                    }}
                    onClick={() =>
                      dispatch(addToCart(item, item.quantity + 1, item.varient))
                    }
                  ></i>
                  <b>{item.quantity}</b>
                  <i
                    className="fa fa-minus"
                    area-hidden="true"
                    style={{
                      color: 'green',
                      fontWeight: 'bold',
                      margin: '5px',
                      cursor: 'pointer',
                    }}
                    onClick={() =>
                      dispatch(addToCart(item, item.quantity - 1, item.varient))
                    }
                  ></i>
                  <hr />
                </div>
                <div className="m-1 w-100">
                  <img
                    src={item.image}
                    style={{ height: '80px', width: '80px' }}
                    alt="photo"
                  />
                </div>
                <div className="m-1 w-100 mt-4">
                  <i
                    className="fa fa-trash"
                    area-hidden="true"
                    style={{
                      color: 'red',
                      fontWeight: 'bold',
                      margin: '5px',
                      cursor: 'pointer',
                    }}
                    onClick={() => {
                      dispatch(deleteFromCart(item));
                    }}
                  ></i>
                </div>
              </div>
            );
          })}
        </div>
        <div className="col-md-4 text-right">
          <h2 style={{ fontSize: '20px' }}>SubTotal = {subTotal} /-Rs</h2>
          <h2 style={{ fontSize: '20px' }}>+ 18% GST</h2>
          <hr />
          <h2 style={{ fontSize: '20px' }}>Total={Total}/-Rs</h2>

          <hr />
          <Checkout subTotal={Total}></Checkout>
        </div>
      </div>
    </div>
  );
};
export default CartScreen;
