import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { registerUser } from '../actions/userActions';
import Loading from '../components/Loading';
import Success from '../components/success';
import Error from '../components/Error';

const RegisterScreen = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmpassword, setconfirmpassword] = useState('');

  const registerState = useSelector((state) => state.registerUserReducer);
  const { error, loading, success } = registerState;

  const nameChangeHandler = (event) => {
    setName(event.target.value);
  };
  const emailChangeHandler = (event) => {
    setEmail(event.target.value);
  };
  const passwordChangeHandler = (event) => {
    setPassword(event.target.value);
  };
  const CpasswordChangeHandler = (event) => {
    setconfirmpassword(event.target.value);
  };

  const dispatch = useDispatch();
  const clickHandler = (event) => {
    if (password !== confirmpassword) {
      alert('password are not matched');
    } else {
      const user = {
        name,
        email,
        password,
        confirmpassword,
      };
      dispatch(registerUser(user));
    }
    event.preventDefault();
  };
  return (
    <div className="register">
      <div className="row justify-content-center mt-5">
        <div className="col-md-5 mt-4 text-right shadow-lg p-5 mb-5 bg-white rounded">
          {loading && <Loading />}
          {success && <Success success="User register successfully"></Success>}
          {error && <Error error="Email already Register"></Error>}
          <h1 className="text-center" style={{ fontSize: '30px' }}>
            Register
          </h1>
          <div>
            <input
              type="text"
              placeholder="Name"
              className="form-contol"
              required
              value={name}
              onChange={nameChangeHandler}
            />
            <input
              type="text"
              placeholder="email"
              className="form-contol"
              required
              value={email}
              onChange={emailChangeHandler}
            />
            <input
              type="password"
              placeholder="Password"
              className="form-contol"
              required
              value={password}
              onChange={passwordChangeHandler}
            />
            <input
              type="password"
              placeholder="Confirm Password"
              className="form-contol"
              required
              value={confirmpassword}
              onChange={CpasswordChangeHandler}
            />
            <button className="btn mt-3 mb-2" onClick={clickHandler}>
              REGISTER
            </button>
            <br />
            <a href="/login" style={{ textDecorationLine: 'none' }}>
              Click here to Login
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
export default RegisterScreen;
