import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getUserOrders } from '../actions/orderActions';
import Loading from '../components/Loading';
import Error from '../components/Error';
import 'aos/dist/aos.css';
import AOS from 'aos';

const OrdersScreen = () => {
  AOS.init({
    duration: 1000,
  });

  const orderState = useSelector((state) => state.userOrdersReducer);
  const { orders, error, loading } = orderState;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getUserOrders());
  }, [dispatch]);

  return (
    <div>
      <h1
        style={{
          fontSize: '32px',
          fontWeight: 'bold',
          color: 'red',
        }}
      >
        My Orders
      </h1>
      <hr />
      <div data-aos="fade-down" className="row justify-content-center">
        {loading && <Loading />}
        {error && <Error error="Something went wrong!" />}
        {orders &&
          orders.map((order, index) => {
            return (
              <div
                className="col-md-8 mt-4 p-1"
                style={{
                  backgroundColor: 'skyblue',
                  borderRadius: '10px',
                  boxShadow:
                    'rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px',
                }}
                key={index}
              >
                <div className="flex-container">
                  <div className="text-left w-100 m-1">
                    <h1 style={{ fontSize: '25px' }}>Items</h1>
                    <hr />
                    {order.orderItems.map((item, index) => {
                      return (
                        <div key={index}>
                          <p>
                            {item.name}[{item.varient}]*{item.quantity}=
                            {item.price}
                          </p>
                        </div>
                      );
                    })}
                  </div>
                  <div className="text-left w-100 m-1">
                    <h1 style={{ fontSize: '25px' }}>Address</h1>
                    <hr />
                    <p>Street : {order.shippingAddress.street}</p>
                    <p>city : {order.shippingAddress.city}</p>
                    <p>country : {order.shippingAddress.country}</p>
                    <p>pincode : {order.shippingAddress.pincode}</p>
                  </div>
                  <div className="text-left w-100 m-1">
                    <h1 style={{ fontSize: '25px' }}>Address</h1>
                    <hr />
                    <p>Order amount : {order.orderAmount} Rs.</p>
                    <p>Date : {order.createdAt.substring(0, 10)}</p>
                    <p>transactionId : {order.transactionId}</p>
                    <p>Order ID : {order._id}</p>
                  </div>
                </div>
              </div>
            );
          })}
      </div>
    </div>
  );
};
export default OrdersScreen;
