import React from 'react';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Navbar from './components/Navbar';
import HomeScreen from './screens/HomeScreen';
import CartScreen from './screens/CartScreen';
import LoginScreen from './screens/LoginScreen';
import RegisterScreen from './screens/RegistrationScreen';
import OrdersScreen from './screens/OrdersScreen';
import AdminScreen from './screens/AdminScreen';
import UserList from './screens/UsersList';
import AddNewPizzasList from './screens/AddNewPizzasList';
import PizzasList from './screens/PizzasList';
import OrdersList from './screens/OrdersList';
import EditPizza from './screens/EditPizza';

function App() {
  return (
    <div className="App">
      <Navbar />
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<HomeScreen />} />
          <Route path="/cart" element={<CartScreen />} />
          <Route path="/register" element={<RegisterScreen />} />
          <Route path="/login" element={<LoginScreen />} />
          <Route path="/orders" element={<OrdersScreen />} />
          <Route path="/admin/*" element={<AdminScreen />}>
            <Route path="users" element={<UserList />} />
            <Route path="addpizza" element={<AddNewPizzasList />} />
            <Route path="pizzas" element={<PizzasList />} />
            <Route path="editpizza/:pizzaid" element={<EditPizza />} />
            <Route path="orders" element={<OrdersList />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
