import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { loginUser } from '../actions/userActions';
import Error from '../components/Error';
import Loading from '../components/Loading';
import { useNavigate } from 'react-router-dom';

const LoginScreen = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const loginState = useSelector((state) => state.loginUserReducer);
  const { loading, error, currentUser } = loginState;

  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    if (localStorage.getItem('currentUser')) {
      navigate('/');
    }
  }, [currentUser, navigate]);

  const emailChangeHandler = (e) => {
    setEmail(e.target.value);
  };
  const passwordChangeHandler = (e) => {
    setPassword(e.target.value);
  };

  const LoginHandler = () => {
    const user = {
      email,
      password,
    };
    dispatch(loginUser(user));
  };

  return (
    <div className="login">
      <div className="row justify-content-center mt-5">
        <div className="col-md-5 mt-4 text-right shadow-lg p-3 mb-5 bg-white rounded">
          <h1 className="text-center" style={{ fontSize: '25px' }}>
            Login
          </h1>
          {loading && <Loading />}
          {error && <Error error="Invailid credential" />}
          <div>
            <input
              type="text"
              placeholder="email"
              className="form-control"
              required
              value={email}
              onChange={emailChangeHandler}
            />
            <input
              type="password"
              placeholder="password"
              className="form-control"
              required
              value={password}
              onChange={passwordChangeHandler}
            />
            <button onClick={LoginHandler} className="btn mt-3 mb-2">
              LOGIN
            </button>
            <br />
            <a
              href="/register"
              style={{ textDecorationLine: 'none' }}
              // className='nav-link'
            >
              Click here to Register
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
export default LoginScreen;
